import React, { Component } from "react";

class App extends Component{


render(){
   
    return <React.Fragment>
	<h1>Welcome to Budget</h1>
	<Navbar/>
	</React.Fragment>;
}
}

export class Navbar extends Component{

    state = {product:[
        {id:1,name:"Pen",quantity:1,price:20},
        {id:2,name:"TV",quantity:2,price:1000},
        {id:3,name:"Fridge",quantity:1,price:5000},
    ],
    total:0


    }

 handleChange = (e,index) => {
var  cust = this.state.product;
cust[index].quantity = e.target.value;
     this.setState({product:cust});
        console.log(e.target.value);
        }


      result = this.state.product.reduce((total, currentValue) => total = total + (currentValue.quantity + currentValue.price),0);

    

    render(){
    
        return <table className="table">
<tr>
    <th>#</th>
    <th>Product Name</th>
    <th>Quantity</th>
    <th>Price</th>
    <th>Net Price</th>
</tr>

{this.state.product.map((prod,index) => <tr key={prod.id}><td>{prod.id}</td><td>{prod.name}</td><td><input type="text" value={prod.quantity} onChange={(e)=>this.handleChange(e,index)}/></td><td><input type="text" value={prod.price}/></td><td>{prod.quantity * prod.price}</td></tr>)}
{console.log(this.result)}


<tr>
<td></td>
<td></td>
<td></td>
<td>Total</td>
<td>{}</td>
</tr>

        </table>;
    }




    }

export default App;